import React, { useEffect, useState } from 'react';
import PageContainer from '../../../../@jumbo/components/PageComponents/layouts/PageContainer';
import { HEADER } from '../../../../@jumbo/constants/HeaderMessages';
import { Box } from '@material-ui/core';
import { NotificationContainer } from 'react-notifications';
import useStyles from '../../Roles/index.style';
import { useHistory } from 'react-router-dom';
import ViewCompliance from './ViewCompliance';
import { useDispatch, useSelector } from 'react-redux';
import { fetchStatutoryCompliance } from '../../../../redux/actions/Compliance';
import ImportStatutoryCompliance from './ImportBulkCompliance';
import StatutoryComplianceTable from './StatutoryComplianceTable';
import AppBar from '@material-ui/core/AppBar';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import Typography from '@material-ui/core/Typography';
import Approvals from './Approvals';
import RoleBasedGuard from '../../../../@jumbo/hocs/RoleAuth';
import { PERMISSIONS } from '../../../../@jumbo/constants/RolesConstants';
import Complied from './Complied';
import Compliances from './Compliances';
import NotComplied from './NotComplied';
import { Grid } from '@material-ui/core';
import { AddCircle } from '@material-ui/icons';
import { FileUpload } from '@mui/icons-material';
import { Link } from 'react-router-dom';
import { Button } from '@material-ui/core';

const breadcrumbs = [
  { label: HEADER.DASHBOARD, link: '/' },
  { label: HEADER.STATUTORY_COMPLIANCE.name, active: false },
];

function a11yProps(index) {
  return {
    id: `simple-tab-${index}`,
    'aria-controls': `simple-tabpanel-${index}`,
  };
}

function TabPanel(props) {
  const { children, value, index, ...other } = props;
  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={`simple-tabpanel-${index}`}
      aria-labelledby={`simple-tab-${index}`}
      {...other}>
      {value === index && (
        <Box p={3}>
          <Typography>{children}</Typography>
        </Box>
      )}
    </div>
  );
}

// TabPanel.propTypes = {
//   children: PropTypes.node,
//   index: PropTypes.any.isRequired,
//   value: PropTypes.any.isRequired,
// };

const StatutoryCompliance = () => {
  const [activeStep, setActiveStep] = useState(0);
  const [file, setFile] = useState('');
  const dispatch = useDispatch();
  const { statutoryComplianceData } = useSelector(({ compliance }) => compliance);
  const [openDialog, setOpenDialog] = useState(false);
  const [openDrawer, setOpenDrawer] = useState(false);
  const [complianceDetails, setComplianceDetails] = useState({});
  const [value, setValue] = React.useState(0);
  const classes = useStyles();
  const history = useHistory();

  // tabs handleChange
  const handleChange = (event, newValue) => {
    setValue(newValue);
  };

  const onCloseDrawer = () => {
    setOpenDrawer(false);
  };
  const onCloseDialog = () => {
    setOpenDialog(false);
    setActiveStep(0);
  };
  const onViewCompliance = data => {
    setComplianceDetails(data);
    setOpenDrawer(true);
  };
  const onUpdateCompliance = data => {
    history.push({ pathname: `statutory-compliance/update`, state: data });
  };
  const onDeleteCompliance = data => {
    //console.log(data);
  };
  useEffect(() => {
    dispatch(fetchStatutoryCompliance());
  }, []);

  return (
    <React.Fragment>
      <PageContainer heading={HEADER.STATUTORY_COMPLIANCE.name} breadcrumbs={breadcrumbs}>
        <Box className={classes.inBuildAppCard}>
          <Box padding={5}>
            {/* tabs */}
            <Box padding={4} style={{ display: 'flex', alignItems: 'center', justifyContent: 'space-between' }}>
              <Grid item md={9} xs={12}>
                <AppBar position="static" color="white">
                  <Tabs value={value} onChange={handleChange} aria-label="simple tabs example">
                    <Tab label="Approved" {...a11yProps(0)} />
                    {/* <RoleBasedGuard permission={PERMISSIONS.STATUTORY_COMPLIANCE.APPROVE}> */}
                    <Tab label="Pending Approval" {...a11yProps(1)} />
                    {/* </RoleBasedGuard> */}
                    <Tab label="Comply" {...a11yProps(2)} />
                <Tab label="Complied" {...a11yProps(3)} />
                    <Tab label="Not Complied" {...a11yProps(4)} />
                  </Tabs>
                </AppBar>
              </Grid>
              <Grid item md={3} xs={12}>
                <Link to={`statutory-compliance/create`}>
                  <Button
                    style={{ marginLeft: '12px' }}
                    variant="contained"
                    size={'small'}
                    className={classes.btn}
                    color="primary"
                    >
                    <AddCircle />
                  </Button>
                </Link>
                <Button
                  style={{ marginLeft: '4px' }}
                  variant="contained"
                  size={'small'}
                  className={classes.btn}
                  onClick={e => setOpenDialog(true)}
                  color="primary"
                  >
                  <FileUpload /> Import
                </Button>
              </Grid>
            </Box>

            <TabPanel value={value} index={0}>
              {/* End of tabs */}
              <StatutoryComplianceTable
                {...{
                  breadCrumbsTitle: HEADER.STATUTORY_COMPLIANCE.name,
                  complianceList: statutoryComplianceData,
                  classes,
                  setOpenDialog,
                  onViewCompliance,
                  onUpdateCompliance,
                  onDeleteCompliance,
                }}
              />
            </TabPanel>
            <TabPanel value={value} index={1}>
              <Approvals
                {...{
                  breadCrumbsTitle: HEADER.STATUTORY_COMPLIANCE.name,
                  complianceList: statutoryComplianceData,
                  classes,
                  setOpenDialog,
                  onViewCompliance,
                  onUpdateCompliance,
                  onDeleteCompliance,
                }}
              />
            </TabPanel>
            <TabPanel value={value} index={2}>
              <Compliances
                {...{
                  breadCrumbsTitle: HEADER.STATUTORY_COMPLIANCE.name,
                  complianceList: statutoryComplianceData,
                  classes,
                  setOpenDialog,
                  onViewCompliance,
                  onUpdateCompliance,
                  onDeleteCompliance,
                }}
              />
            </TabPanel>
            <TabPanel value={value} index={3}>
              <Complied
                {...{
                  breadCrumbsTitle: HEADER.STATUTORY_COMPLIANCE.name,
                  complianceList: statutoryComplianceData,
                  classes,
                  setOpenDialog,
                  onViewCompliance,
                  onUpdateCompliance,
                  onDeleteCompliance,
                }}
              />
            </TabPanel>
            <TabPanel value={value} index={4}>
              <NotComplied
                {...{
                  breadCrumbsTitle: HEADER.STATUTORY_COMPLIANCE.name,
                  complianceList: statutoryComplianceData,
                  classes,
                  setOpenDialog,
                  onViewCompliance,
                  onUpdateCompliance,
                  onDeleteCompliance,
                }}
              />
            </TabPanel>
          </Box>
        </Box>
      </PageContainer>
      <ImportStatutoryCompliance
        {...{
          breadCrumbsTitle: HEADER.STATUTORY_COMPLIANCE.name,
          openDialog,
          onCloseDialog,
          activeStep,
          setActiveStep,
          file,
          setFile,
        }}
      />
      <ViewCompliance {...{ complianceDetails, openDrawer, onCloseDrawer }} />
      <NotificationContainer />
    </React.Fragment>
  );
};

export default StatutoryCompliance;
